import Vue from 'vue'
import Vuex from 'vuex'
import bgimg1 from '@/assets/style/images/musicplayerbgimg.png';
import plimg1 from '@/assets/style/images/comment_01.jpg';
import img1 from '@/assets/style/images/timg.jpg';
import song1 from '@/assets/style/mp3/red.mp3';
import smimg1 from '@/assets/style/images/home-player_01.png';
Vue.use(Vuex)

const state = {
     count: 0,
     songname:'Red',
     bgimg:bgimg1,
     songimg:img1,
     songername:'Taylor Swift',
     plimgs:plimg1,
     music:song1,
     smimgs:smimg1,
}
const mutations={
     increment (state) {
       state.count++
     }
   }
   
 
 export default new Vuex.Store({
  state,
  mutations
})
